import React from "react";

class EatingBoard extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div id="backgroundBoard">
        {!this.props.lastBidding && (
          <button className="boardButton" onClick={this.props.onClick}>
            <img
              id="emptyBoard"
              src="https://i.imgur.com/7IUT4ST.png"
              alt="board0"
              hidden={!(this.props.placed.length === 0)}
            ></img>
            <img
              id="fuguBoard"
              src="https://i.imgur.com/dKdsH8k.png"
              alt="board1"
              hidden={!(this.props.placed.length === 1)}
            ></img>
            <img
              id="fuguBoard"
              src="https://i.imgur.com/KPxm6pm.png"
              alt="board2"
              hidden={!(this.props.placed.length === 2)}
            ></img>
            <img
              id="fuguBoard"
              src="https://i.imgur.com/fXntS4Z.png"
              alt="board3"
              hidden={!(this.props.placed.length === 3)}
            ></img>
            <img
              id="fuguBoard"
              src="https://i.imgur.com/jWC9bxQ.png"
              alt="board4"
              hidden={!(this.props.placed.length === 4)}
            ></img>
          </button>
        )}
        {this.props.lastBidding && (
          <img
            src="https://i.imgur.com/y1DXfJk.png"
            alt="Welcome to FUGU"
            widt="30px"
            heigh="30px"
          />
        )}
        <img
          id="chopSticks"
          src="https://i.imgur.com/3hjNxRF.png"
          alt="chopstick2"
          hidden={!(this.props.score === 2)}
        ></img>
        <img
          id="chopSticks"
          src="https://i.imgur.com/gapO3dn.png"
          alt="chopstick1"
          hidden={!(this.props.score === 1)}
        ></img>
        <img
          id="chopSticks"
          src="https://i.imgur.com/7ucOo7F.png"
          alt="chopstick0"
          hidden={!(this.props.score === 0)}
        ></img>
        <button onClick={this.props.eatOwn} hidden={!this.props.lastBidding}>
          {" "}
          Eat Your sushis
        </button>
      </div>
    );
  }
}

export default EatingBoard;
